<template  dir="rtl">
    <div class="madars">
        <!-- <h1>Ÿà€åÿ±ÿß€åÿ¥ Ÿæÿ±Ÿà⁄òŸá</h1> -->
        <form @submit.prevent="handleSubmit">
        <label>ŸÖŸàÿ∂Ÿàÿπ:</label>
        <input  v-model="title" type="text" required>
        <label>ÿ¨ÿ≤ÿ¶€åÿßÿ™:</label>
        <textarea  v-model="details"></textarea>
        <select v-model="day">
            <option value="one">ÿ±Ÿàÿ≤ ÿßŸàŸÑ</option>
            <option value="two">ÿ±Ÿàÿ≤ ÿØŸàŸÖ</option>
            <option value="three">ÿ±Ÿàÿ≤ ÿ≥ŸàŸÖ</option>
        </select>
        <button>ÿ®ÿ±Ÿàÿ≤ÿ±ÿ≥ÿßŸÜ€å Ÿæÿ±Ÿà⁄òŸá</button>
    </form>
    </div>
</template>

<script>
const apiUrl = process.env.VUE_APP_API_URL;

export default {
    props : ['id'],
    data(){
        return{
            title : '',
            details : '',
            day : '',
            uri :  `${apiUrl}/projects/` + this.id
        }
    },
    mounted(){
    fetch(this.uri)
        .then(res => res.json())
        .then(data => {
            this.title = data.title
            this.details = data.details
            this.day = data.day
        }).catch(err => console.log(err.message))
    },
methods:{
    handleSubmit(){
        fetch(this.uri,{
            method : 'PATCH',
            headers : {'Content-Type' : 'application/json'},
            body : JSON.stringify({title : this.title , details : this.details, day : this.day})
        })
        .catch(err => console.log(err.message))
        this.$router.push('/')
     },
}}

</script>

<style>
.madars {
  display: flex;
  justify-content: center;
  padding: 20px;
}

form {
  background: #B0DB9C;
  padding: 20px;
  border-radius: 10px;
  width: 60%;
  margin: 0 auto;
}

label {
  display: block;
  color: #555;
  font-weight: bold;
  font-size: 14px;
  margin: 20px 0 10px 0;
  text-transform: uppercase;
  letter-spacing: 1px;
}

input,
textarea,
select {
  padding: 10px;
  border-radius: 10px;
  border: 1px solid #ddd;
  width: 100%;
  box-sizing: border-box;
  background: #DDF6D2;
}

textarea {
  height: 130px;
}

select {
  width: 4.5rem;
  text-align: center;
}

textarea {
  height: 150px;
  resize: vertical;
}

select {
  width: auto;
  min-width: 120px;
}
</style>